import React from 'react';

import logoImage from 'assets/images/logo.png';
import {Logo, Container, Navigation} from 'ui';

export const Header = () => (
    <div>
        <Container justify='space-between' align='center'>
            <Logo img={logoImage} src='#' alt='Hive'/>
            <Navigation>
                <Navigation.Item>Problem</Navigation.Item>
                <Navigation.Item>Solution</Navigation.Item>
                <Navigation.Item>Features</Navigation.Item>
                <Navigation.Item>Technology</Navigation.Item>
                <Navigation.Item>Market</Navigation.Item>
                <Navigation.Item>Token Sale</Navigation.Item>
                <Navigation.Item>Leadership</Navigation.Item>
            </Navigation>
        </Container>
    </div>
);


